import React from 'react';
import {Switch, Route} from 'react-router-dom';
import Layout from "./component/Hoc/Layout";
import SignIn from "./component/Account/SignIn";
import Register from "./component/Account/Register";
import ReadPost from "./component/Home/ReadPost";
import Home from "./component/Home/index";
import Auth from "./component/Hoc/Auth";
class Routes extends React.Component{
  
  render(){
    return (
      <Layout>
        <Switch>
          <Route exact path="/postsquare-build/signin"  component={SignIn}  />
          <Route exact path="/postsquare-build/register" component={Register} />
          <Route exact path="/postsquare-build/home" component={Auth(Home)} />
          <Route exact path="/postsquare-build" component={ReadPost}/>
        </Switch>
      </Layout>
    )
  }
}

export default (Routes);
