import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import ResponsiveMenu from 'react-responsive-navbar';
import { logoutUser } from "../../actions/index";

class Header extends Component {
  handleLogout=()=>{
    this.props.logoutUser()
  }
  render() {
    return (
      <ResponsiveMenu
        menuOpenButton={<div className="nav-toggle" >Open</div>}
        menuCloseButton={<div className="nav-toggle" >Close</div>}
        changeMenuOn="500px"
        largeMenuClassName="large-menu-classname"
        smallMenuClassName="small-menu-classname"
        menu={
        <ul id="ul" >        
          {(!this.props.currentUser) ? 
            <>
                <li><Link to="/postsquare-build/register">Register</Link></li>
                <li><Link to="/postsquare-build/signin">SignIn</Link></li>
            </>
            :
            <> 
                <li><Link to='/postsquare-build/'>{this.props.currentUser.email}</Link></li>
                <li><Link to='/postsquare-build/signin'className="logout" onClick={this.handleLogout}>Logout</Link></li>
                <li><Link to="/postsquare-build/home">Home</Link></li>
            </>
          }
            <li><Link to="/postsquare-build/">Posts</Link></li>
            <li id="logo" ><Link to="/postsquare-build/">Postsquare</Link></li>
        </ul>
        }
      />
    )
  }
}
const mapStateToProps=(state)=>{
  return {currentUser:state.signIn.currentUser}
}
export default connect(mapStateToProps,{logoutUser})(Header);
